@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;700&display=swap");

* {
  font-family: "Quicksand", sans-serif;
  margin: 0;
}

/* App */
.App {
  display: grid;
  grid-template-rows: 10% auto 10%;
}

.mobile_view {
  display: none;
}

/* Barre de navigation */
.navbar {
  display: flex;
  width: 100%;
  background: #f9f9f9;
  transition: 0.6s;
  z-index: 11;
}

.mobile_navbar,
.overlay_background,
.mobile_footer {
  display: none;
}

.navbar .navbar_logo img,
.footer .footer_logo img {
  max-height: 25px;
  max-width: 110px;
  padding: 10px;
}
.navbar .navbar_logo img {
  margin-top: 4%;
  margin-left: 50px;
}

.footer .footer_logo img {
  margin-top: 6%;
  margin-left: 40px;
}

.navbar .navbar_links {
  margin-top: 1%;
  padding: 10px;
  margin-left: auto;
  transition: 0.6s;
}

.navbar .navbar_links a {
  text-decoration: none;
  color: #888888;
  margin-inline: 12px;
  font-weight: 500;
}
.navbar .navbar_links a.is_active {
  color: #4a90e2 !important;
  text-decoration: underline !important;
}
.navbar .navbar_links a:hover {
  color: #4a90e2;
  text-decoration: underline;
}
.navbar .navbar_button {
  margin-top: 20px;
  margin-right: 1%;
  background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
    0% no-repeat padding-box;
  border-radius: 7px;
  height: 35px;
}
.navbar .navbar_button button {
  height: 32px;
  color: #888888;
  font-weight: 500;
  border-radius: 7px;
  cursor: pointer;
}

.navbar .translations_buttons {
  margin-top: 2%;
  margin-right: 1%;
}

.navbar .translations_buttons a {
  margin-right: 5px;
}
.navbar .translations_buttons a img:hover {
  opacity: 0.4;
}

/* Footer */
.footer {
  display: grid;
  grid-template-rows: 80% 20%;
  background: #f9f9f9;
  height: 85px;
  z-index: 10;
}

.footer_first_section {
  display: grid;
  grid-template-columns: 10% 70% auto;
}

.footer .footer_links {
  margin-top: 2%;
  text-align: center;
}

.footer .footer_links a {
  margin-inline: 10px;
  text-decoration: none;
  color: #888888;
  font-weight: 400;
}

.footer .footer_links a:hover {
  font-weight: 500;
}

.footer .footer_socials {
  display: flex;
  margin-top: 7%;
}

.footer .footer_socials a {
  margin-inline: 5px;
  text-decoration: none;
  color: #4a90e2;
}
.footer .footer_socials a:hover {
  color: #306198;
}

.footer .footer_contact button.footer_contact_button {
  border: 2px solid #4a90e2;
  color: #4a90e2;
  background: #fff;
  border-radius: 5px;
  cursor: pointer;
}
.footer .footer_website_reference {
  display: block;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
  color: #888888;
  font-size: 12px;
}

/* Dropdown */
.dropdown_container {
  position: absolute;
  z-index: 15;
  width: 150px;
  height: 120px;
  top: 7%;
  left: 71%;
  background: transparent linear-gradient(356deg, #4a90e2 0%, #c471ed 100%) 0%
    0% no-repeat padding-box;
  box-shadow: 4px 8px 36px #0000001f;
  border-radius: 10px;
  opacity: 1;
}

.dropdown_content {
  width: 141px;
  height: 111px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 4px 8px 36px #0000001f;
  border: 2px solid #222222;
  border-radius: 10px;
  opacity: 1;
}
.dropdown_content p {
  margin-top: 3%;
  margin-left: 4%;
}
.dropdown_content p a {
  text-decoration: none;
  color: #888888;
}

.dropdown_content p a:hover {
  color: #3c3b3b;
}

/* Modal de creation de compte */
.modal_background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 17;
  padding-top: 10%;
}

.modal_container {
  display: grid;
  grid-template-rows: 10% auto;
  max-width: 600px;
  margin: 0 auto;
  padding: 5px 20px;
  background: white;
  border-radius: 7px;
  text-align: center;
}

.modal_close button {
  float: right;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background: red;
  color: white;
  font-weight: 700;
  border: none;
  cursor: pointer;
}

.modal_close button:hover {
  background: rgb(161, 1, 1);
}

/* Content structure */

.app_content,
.global_container,
.global_container_content {
  width: 100%;
  box-sizing: border-box;
}

.app_content {
  height: 550px;
}

.global_container_content {
  grid-template-rows: auto;
}

/* products page */
.product_wrapper {
  grid-row-start: auto;
  width: 100%;
  /*height: 600px;*/
  overflow: hidden;
  display: grid;
  grid-template-columns: 50% 50%;
}

.product_wrapper.bg-dark {
  background: transparent linear-gradient(90deg, #f9f9f9 0%, #cecece00 100%) 0%
    0% no-repeat padding-box;
  opacity: 1;
}

.product_wrapper .product_wrapper_left {
  grid-column-start: 1;
  grid-column-end: 1;
  padding-top: 10%;
  padding-left: 10%;
}

h1.wrapper_title {
  font-size: 60px;
}

p.wrapper_text {
  color: #888888;
  font-size: 26px;
  margin-top: 2%;
}

.product_wrapper_left .wrapper_buttons {
  margin-top: 3%;
  display: grid;
  grid-template-columns: max-content;
}

.wrapper_buttons .wrapper_button_ios {
  grid-column-start: 1;
  grid-column-end: 1;
  width: 100%;
}

.wrapper_button_ios a img.ios {
  margin-right: 10px;
  max-width: 162px;
}

.wrapper_buttons .wrapper_button_google {
  grid-column-start: 2;
  grid-column-end: 3;
  width: 100%;
}

.wrapper_button_google a img.google {
  width: 162px;
}

.product_wrapper .product_wrapper_right {
  padding-left: 25%;
}
.product_wrapper .product_wrapper_right.pay {
  padding-left: 10%;
}

.product_wrapper_right img {
  max-height: 520px;
}

.crypto_doc_wrapper {
  padding-top: 7%;
  text-align: center !important;
  width: 100%;
}
.crypto_doc_wrapper .crypto_doc_first_section {
  text-align: center;
}

.crypto_doc_wrapper .crypto_doc_first_section h1 {
  font-size: 60px;
  font-weight: 700;
  color: #222222;
}

.crypto_doc_wrapper .crypto_doc_first_section p {
  color: #888888;
  font-size: 26px;
  margin-top: 1%;
  margin-bottom: 2%;
}
.crypto_doc_first_section .button_container {
  width: 336px;
  height: 54px;
  background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
    0% no-repeat padding-box;
  border-radius: 7px;
  margin-left: auto;
  margin-right: auto;
}
.crypto_doc_first_section .button_container button.crypto_doc_link_create {
  text-decoration: none;
  color: #222222;
  padding: 7px;
  border-radius: 7px;
  font-size: 20px;
  width: 336px;
  height: 50px;
  cursor: pointer;
}

.crypto_doc_wrapper .crypto_doc_buttons {
  display: flex;
  margin-top: 2%;
  /* margin-left: 39%; */
}

.crypto_doc_buttons .crypto_doc_buttons_off {
  margin-right: auto;
  margin-left: auto;
}
.crypto_doc_buttons .crypto_doc_buttons_off img.ios {
  margin-right: 5px;
}
.crypto_doc_buttons .crypto_doc_buttons_off img.ios,
.crypto_doc_buttons .crypto_doc_buttons_off img.google {
  max-width: 162px;
}

.transfer_wrapper {
  height: 600px;
  display: grid;
  grid-template-columns: 50% 50%;
}

.transfer_wrapper_left {
  height: 550px;
  display: grid;
  grid-template-rows: 50% 50%;
}
.transfer_wrapper_left_first_section {
  display: grid;
  grid-template-columns: 50% 50%;
}
.transfer_wrapper_left_first_section img {
  grid-column-start: 2;
  height: 100%;
}
.transfer_wrapper_left_second_section {
  padding-left: 2%;
}
.transfer_wrapper_left_second_section h1,
.transfer_wrapper_right_first_section h1 {
  color: #222222;
  font-size: 72px;
}
.transfer_wrapper_left_second_section p,
.transfer_wrapper_right_first_section p {
  color: #888888;
  font-size: 26px;
}
.transfer_button {
  margin-top: 2%;
  width: 225px;
  height: 54px;
  background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
    0% no-repeat padding-box;
  border-radius: 7px;
}
.transfer_button button.transfer_button_learn_more {
  text-decoration: none;
  color: #222222;
  padding: 7px;
  border-radius: 7px;
  font-size: 20px;
  width: 225px;
  height: 50px;
  cursor: pointer;
}
.transfer_wrapper_right {
  height: 550px;
  display: grid;
  grid-template-rows: 50% 50%;
  padding-left: 10%;
}
.transfer_wrapper_right .transfer_wrapper_right_second_section img {
  max-width: 450px;
}

/* Dev part */
.wrapper_links {
  margin-top: 2%;
}
.wrapper_links button.api_doc_button {
  width: 340px;
  height: 54px;
  border: 2px solid #4a90e2;
  border-radius: 7px;
  opacity: 1;
  background: #fff;
  color: #4a90e2;
  cursor: pointer;
}

/* Pricing part */
.pricing_wrapper {
  display: grid;
  width: 100%;
  grid-template-columns: 20% 60% 20%;
}

.pricing_content {
  margin-top: 7%;
  height: 400px;
  background: #f9f9f9;
  border-radius: 25px;
  display: grid;
  grid-template-columns: 50% 50%;
  box-shadow: 0px 8px 10px #00000026;
}

.pricing_content_left {
  margin-top: 2%;
  margin-left: 4%;
}
.pricing_content_left h1 {
  color: #222222;
  font-size: 50px;
  margin-top: 2%;
}
.pricing_content_left p {
  margin-top: 6%;
  color: #888888;
  font-size: 26px;
}

.pricing_button_get_now {
  margin-top: 4%;
  text-align: center;
  width: 336px;
  height: 54px;
  background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
    0% no-repeat padding-box;
  border-radius: 7px;
}

button.get_now_button {
  text-decoration: none;
  color: #222222;
  padding: 7px;
  border-radius: 7px;
  font-size: 20px;
  width: 336px;
  height: 50px;
  cursor: pointer;
}

.pricing_content_right img {
  padding-top: 6%;
  padding-right: 6%;
}

.pricing_offer_content {
  display: grid;
  grid-template-columns: 49% auto 49%;
  margin-top: 5%;
}

.offer_content_left,
.offer_content_right {
  background: #f9f9f9;
  border-radius: 25px;
  padding-top: 20%;
  padding-left: 4%;
  height: 400px;
  box-shadow: 0px 8px 10px #00000026;
}

.offer_content_left small,
.offer_content_right small {
  color: #888888;
  font-weight: 700;
}
.offer_content_left h2,
.offer_content_right h2 {
  color: #000000;
  font-size: 50px;
}
.offer_content_left ul,
.offer_content_right ul {
  color: #888888;
  line-height: 30px;
  font-weight: 500;
}

.offer_content_left .offer_button_container,
.offer_content_right .offer_button_container {
  margin-top: 4%;
  text-align: center;
  width: 300px;
  height: 54px;
  background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
    0% no-repeat padding-box;
  border-radius: 7px;
}
.offer_button_container button.offer_button,
.offer_button_container button.offer_button {
  text-decoration: none;
  color: #222222;
  padding: 7px;
  border-radius: 7px;
  font-size: 20px;
  width: 300px;
  height: 50px;
  cursor: pointer;
}

/* Package FullPage */

.fp-tableCell {
  display: flex !important;
}

#fp-nav {
  top: 30% !important;
}

/*
