@media screen and (max-width: 640px) {
  .fp-enabled body,
  html.fp-enabled {
    overflow: auto !important;
  }
  .App,
  .overlay_background {
    display: block;
  }
  .app_content,
  .mobile_view {
    display: block !important;
  }
  .app_content {
    height: 800px;
  }
  .navbar,
  .footer,
  .web_view,
  #fp-nav ul,
  .fp-slidesNav ul {
    display: none;
  }

  .mobile_app_content {
    display: block;
    margin-top: 20px;
  }

  /* Menu Overlay */

  .overlay_background {
    position: fixed;
    top: 0;
    left: 0;
    width: 55%;
    height: 100%;
    background: rgba(0, 0, 0, 0.875);
    z-index: 18;
  }

  .overlay_container {
    display: grid;
    grid-template-rows: 30% auto 20%;
  }
  .overlay_close {
    width: 100%;
  }
  .overlay_close button {
    float: right;
    background: rgb(198, 2, 2);
    border-radius: 50%;
    color: white;
    font-weight: 700;
    border: none;
    margin-right: 5%;
    margin-top: 5%;
    height: 30px;
    width: 30px;
  }
  .overlay_content {
    margin-left: 10%;
  }
  .overlay_content p {
    line-height: 30px;
  }
  .overlay_content a {
    text-decoration: none;
    color: white;
    font-weight: 700;
  }

  .overlay_button {
    margin-top: 10%;
    margin-left: 10%;
    background: transparent linear-gradient(109deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    height: 35px;
    width: 150px;
  }

  .overlay_button button {
    height: 32px;
    width: 147px;
    border-radius: 7px;
    border: none;
    text-align: center;
    font-weight: 700;
  }

  /* Carousel */
  .carousel .slide {
    text-align: initial !important;
  }

  .carousel .slide img {
    width: auto !important;
  }

  .carousel img {
    width: auto !important;
  }

  .carousel.carousel-slider .control-arrow {
    display: none;
  }

  .carousel .thumbs-wrapper {
    display: none;
  }

  .carousel .carousel-status {
    display: none !important;
  }

  .carousel .control-dots {
    width: auto !important;
    top: 45% !important;
  }

  .carousel .control-dots .dot {
    display: block !important;
    margin: 10px 8px !important;
    background: #000 !important;
  }

  /* Barre de navigation mobile */
  .mobile_navbar {
    display: grid;
    grid-template-columns: 20% 60% auto;
    background: #f9f9f9 !important;
    height: 60px;
  }
  .mobile_navbar_menu_button_container {
    margin-top: 20%;
    margin-right: auto;
    margin-left: auto;
  }
  .mobile_navbar_logo {
    margin-right: auto;
    margin-left: 25%;
    margin-top: 5%;
  }
  .mobile_navbar_logo img {
    max-height: 30px;
  }
  .mobile_navbar_menu_button_container {
    width: 32px;
    height: 34px;
    background: transparent linear-gradient(180deg, #4a90e2 0%, #c471ed 100%) 0%
      0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
  }
  .mobile_navbar_menu_button_container button {
    width: 30px;
    height: 32px;
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 2px solid #000000;
    border-radius: 4px;
    opacity: 1;
    cursor: pointer;
  }

  .mobile_navbar .translations_buttons {
    margin-top: 25%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .mobile_navbar .translations_buttons a {
    margin-right: 7%;
  }

  /*  Footer mobile */
  .mobile_footer {
    background: #f9f9f9;
    display: grid;
    width: 100% !important;
    bottom: 0 !important;
    position: absolute;
  }
  .mobile_footer .img img {
    margin-left: 2%;
    margin-top: 2%;
    max-height: 20px;
  }
  .mobile_footer .links p a {
    text-decoration: none;
    color: #888888;
    font-weight: 500;
  }
  .mobile_footer .contacts {
    margin-left: 2%;
    margin-top: 2%;
  }
  .mobile_footer .contacts button {
    margin-left: 45%;
    border: 2px solid #4a90e2;
    border-radius: 5px;
    opacity: 1;
    color: #4a90e2;
    background: #fff;
  }
  .mobile_footer .contacts a {
    text-decoration: none;
    color: #4a90e2;
    padding-right: 5%;
  }
  .footer_copyright {
    width: 100%;
    margin-top: 2%;
    text-align: center;
    color: #888888;
    padding: 4px;
    font-weight: 500;
    font-size: 14px;
  }

  /* Produit mobile */
  .mobile_product_wrapper {
    display: block;
    height: 100%;
  }
  .mobile_product_wrapper .notabank {
    display: block;
  }
  .mobile_product_wrapper .sendmoney {
    background: #f4f4f4;
    display: block;
  }
  .mobile_product_wrapper .notabank .first {
    padding: 2%;
  }
  .mobile_product_wrapper .sendmoney .first {
    padding: 4%;
  }
  .mobile_product_wrapper .notabank .second,
  .mobile_product_wrapper .sendmoney .second,
  .mobile_product_wrapper .crypto .second {
    text-align: center;
  }
  .mobile_product_wrapper .transfer .first {
    padding: 4%;
  }
  .mobile_product_wrapper .transfer .second {
    padding: 1%;
  }

  .mobile_product_wrapper .crypto {
    display: block;
    background: #f4f4f4;
    height: 100%;
  }
  .mobile_product_wrapper .crypto .first,
  .mobile_product_wrapper .crypto_doc .first {
    padding: 2%;
  }

  .mobile_product_wrapper .crypto_doc {
    display: block;
  }

  .mobile_product_wrapper .transfer .first .transfer_button,
  .mobile_product_wrapper .transfer .second .transfer_button {
    width: 200px;
    height: 38px;
    background: transparent linear-gradient(115deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    opacity: 1;
  }

  .mobile_product_wrapper .transfer .first .transfer_button button,
  .mobile_product_wrapper .transfer .second .transfer_button button {
    width: 198px;
    height: 35px;
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 2px solid #222222;
    border-radius: 7px;
    opacity: 1;
  }
  .mobile_product_wrapper .crypto_doc .first .crypto_doc_button {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4%;
    width: 300px;
    height: 38px;
    background: transparent linear-gradient(115deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    opacity: 1;
  }
  .mobile_product_wrapper .crypto_doc .first .crypto_doc_button button {
    width: 296px;
    height: 34px;
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 2px solid #222222;
    border-radius: 7px;
    opacity: 1;
  }

  .mobile_product_wrapper .crypto_doc .second .crypto_doc_links {
    text-align: center;
  }
  .mobile_product_wrapper .crypto_doc .second .crypto_doc_links a img {
    max-width: 120px;
  }

  /* developpers mobile */
  .mobile_dev_wrapper {
    display: block;
  }
  .default_payment {
    display: block;
    height: 100%;
  }
  .default_payment.bg {
    background: #f4f4f4;
    height: 100%;
  }
  .default_payment .first {
    padding: 4%;
  }
  .default_payment .second {
    text-align: center;
  }

  .default_button {
    margin-top: 4%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }
  .default_button_wordpress {
    margin-top: 6%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    background: transparent linear-gradient(107deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    opacity: 1;
    width: 250px;
    height: 40px;
  }
  .default_button_wordpress button,
  .mobile_wallet .get_now_button button {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 2px solid #222222;
    border-radius: 7px;
    opacity: 1;
    width: 246px;
    height: 36px;
  }
  .default_button button.api {
    width: 250px;
    height: 45px;
    border: 2px solid #4a90e2;
    border-radius: 7px;
    opacity: 1;
    background: #fff;
  }

  /* pricing mobile */
  .mobile_pricing_wrapper,
  .mobile_wallet,
  .mobile_wallet .first {
    display: block;
  }
  .mobile_wallet {
    margin-top: 4%;
  }
  .mobile_wallet .first {
    padding: 4%;
  }
  .mobile_wallet .card_first {
    text-align: center;
  }
  .mobile_wallet .get_now_button {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4%;
    text-align: center;
    background: transparent linear-gradient(107deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    opacity: 1;
    width: 250px;
    height: 40px;
  }

  .mobile_offer_basic {
    margin-left: auto;
    margin-right: auto;
    margin-top: 8%;
    background: #f4f4f4 0% 0% no-repeat padding-box;
    box-shadow: 0px 8px 24px #00000026;
    border-radius: 7px;
    opacity: 1;
    width: 329px;
    height: 270px;
  }
  .mobile_offer_basic .button_account,
  .mobile_offer_premium .button_account {
    margin-left: auto;
    margin-right: auto;
    margin-top: 7%;
    width: 270px;
    height: 40px;
    background: transparent linear-gradient(111deg, #c471ed 0%, #4a90e2 100%) 0%
      0% no-repeat padding-box;
    border-radius: 7px;
    opacity: 1;
  }

